<template>
  <div class="keranjang">
    <navbar :updateKeranjang="keranjangs" />
    <div class="container">
      <div class="row mt-4">
        <div class="col">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <router-link to="/" class="text-dark">Home </router-link>
              </li>
              <li class="breadcrumb-item">
                <router-link to="/foods" class="text-dark">Foods </router-link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                Keranjang
              </li>
            </ol>
          </nav>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <h2>Keranjang <strong>Saya</strong></h2>
          <div class="responsive mt-3">
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Foto</th>
                  <th scope="col">Judul</th>
                  <th scope="col">Catatan</th>
                  <th scope="col">Jumlah</th>
                  <th scope="col">Harga</th>
                  <th scope="col">Total Harga</th>
                  <th scope="col">Hapus</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(keranjang, index) in keranjangs"
                  :key="keranjang.id"
                >
                  <th>{{ index + 1 }}</th>
                  <td>
                    <img
                      :src="require(`../assets/${keranjang.products.gambar}`)"
                      class="img-fluid shadow"
                      width="250"
                    />
                  </td>
                  <td>
                    <strong>{{ keranjang.products.judul }}</strong>
                  </td>
                  <td>
                    {{ keranjang.keterangan ? keranjang.keterangan : "-" }}
                  </td>
                  <td>{{ keranjang.jumlah_pemesanan }}</td>
                  <td>Rp. {{ keranjang.products.harga }}</td>
                  <td>
                    <strong
                      >Rp.
                      {{
                        keranjang.products.harga * keranjang.jumlah_pemesanan
                      }}</strong
                    >
                  </td>
                  <td align="center" class="text-danger">
                    <b-icon-trash
                      @click="hapusKeranjang(keranjang.id)"
                    ></b-icon-trash>
                  </td>
                </tr>
                <tr>
                  <td colspan="6" align="right">
                    <strong>Total Harga : </strong>
                  </td>
                  <td>
                    <strong>Rp. {{ totalHarga }}</strong>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="row justify-content-end"></div>
      <div class="col-md-4"></div>
       <form class: v-on:submit.prevent>
          <div class="form-group mt-3">
          <label for="nama">Nama :</label>
          <input type="text" class="form-control" v-model="pesan.nama"/>
          </div>
          <div class="form-group mt-3">
          <label for="alamat">Alamat :</label>
          <input type="text" class="form-control" v-model="pesan.alamat"/>
          </div>
          <button type="submit" class="btn btn-light float right" @click="checkout"><b-icon-cart></b-icon-cart>Pesan</button>
          </form>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import axios from "axios";

export default {
  name: "Keranjang",
  components: {
    Navbar,
  },
  data() {
    return {
      keranjangs: [],
      pesan: {}
    };
  },
  methods: {
    setKeranjangs(data) {
      this.keranjangs = data;
    },
    hapusKeranjang(id) {
      axios
        .delete("http://localhost:3000/keranjangs/" + id)
        .then(() => {
          this.$toast.error("Berhasil Hapus", {
            type: "error",
            position: "top-right",
            duration: 3000,
            dismissible: true,
          });

          // Update_Keranjang
          axios
            .get("http://localhost:3000/keranjangs")
            .then((response) => this.setKeranjangs(response.data))
            .catch((error) => console.log(error));
        })
        .catch((error) => console.log(error));
    },
    checkout() {
      if(this.pesan.alamat && this.pesan.nama) {

        this.pesan.keranjangs = this.keranjangs;
        axios
        .post("http://localhost:3000/pesanans", this.pesan)
        .then(()=> {
        
        // Hapus Semua Keranjang
        this.keranjangs.map(function(item){
          return axios
        .delete("http://localhost:3000/keranjangs/" + item.id)
        .catch((error) => console.log(error));
        })
        
        this.$router.push({path: "/pesanan-sukses"})
        this.$toast.success("Sukses Dipesan", {
        type: 'success',
        position:'top-right',
        duration: 3000,
        dismissible: true,
        }); 
        })
        .catch((err) => console.log(err));
        
      }else{
        this.$toast.error("Nama dan Nomor Meja Harus Diisi", {
            type: "error",
            position: "top-right",
            duration: 3000,
            dismissible: true,
          });
      };
    }
  },
  mounted() {
    axios
      .get("http://localhost:3000/keranjangs")
      .then((response) => this.setKeranjangs(response.data))
      .catch((error) => console.log(error))
  },
  computed: {
    totalHarga() {
      return this.keranjangs.reduce(function(items, data) {
        return items + (data.products.harga*data.jumlah_pemesanan)
      }, 0);
    },
  },
};
</script>

