<template>
  <div class="book-detail">
    <navbar :updateKeranjang="keranjangs" />
    <div class="container">
      <!-- breadcrumb -->
      <div class="row mt-5">
        <div class="col">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <router-link to="/" class="text-dark">Home </router-link>
              </li>
              <li class="breadcrumb-item">
                <router-link to="/daftarbuku" class="text-dark">Daftar Buku</router-link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">Order</li>
            </ol>
          </nav>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 mt-4">
          <img :src="require(`../assets/${product.gambar}`)"  class="img-fluid shadow" width="400" />
        </div>
        <div class="col-md-6">
          <h2>
            <strong>{{ product.judul }}</strong>
          </h2>
          <hr>
          <h2>
            <strong>Penulis : </strong>{{ product.penulis }}
          </h2>
          <h4>
            <strong>Rp. {{ product.harga }}</strong>
          </h4>
          <form class: v-on:submit.prevent>
          <div class="form-group mt-3">
          <label for="keterangan">Jumlah Pesan (Maksimal 1 Item)</label>
          <input type="number" class="form-control" max="1" min="0" v-model="pesan.jumlah_pemesanan"/>
          </div>
          <div class="form-group">
          <label for="keterangan">Keterangan</label>
          <textarea v-model="pesan.keterangan" class="form-control" placeholder="Catatan"></textarea>
          </label>
          </div>
          <button type="submit" class="btn btn-light" @click="pemesanan"><b-icon-cart></b-icon-cart> Pesan</button>
          <router-link to="/daftarbuku" class="btn btn-light float-right"
            ><b-icon-bag-plus></b-icon-bag-plus>
            Ayo Pesan Lagi 
          </router-link> 
          </form>
          <div class="container">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Navbar from "@/components/Navbar.vue";
import axios from "axios";

export default {
  name: "DetailBuku",
  components: {
    Navbar,
  },
  data() {
    return {
      product: {},
      pesan: {},
    };
  },
  methods: {
    setProduct(data) {
      this.product = data;
    },
    pemesanan() {
      if(this.pesan.jumlah_pemesanan){
        this.pesan.products = this.product;
        axios
        .post("http://localhost:3000/keranjangs", this.pesan)
        .then(()=> {
        this.$toast.success("Sukses Cek Keranjang Kamo", {
        type: 'success',
        position:'top-right',
        duration: 3000,
        dismissible: true,
        }); 
        })
        .catch((err) => console.log(err));
      } else {
        this.$toast.error("Jumlah Pesanan Harus Diisi", {
        type: 'error',
        position:'top-right',
        duration: 3000,
        dismissible: true,
        });
      }
    },
  },
  mounted() {
    axios
      .get("http://localhost:3000/products/" + this.$route.params.id)
      .then((response) => this.setProduct(response.data))
      .catch((error) => console.log(error));
  },
};
</script>
<style></style>
